
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
name: 'mapSelects',
components: {
Flexbox,
Loading
},
data () {
return {
showDialog: false,
selectBuild: ''
};
},
mounted () {
let _this = this;
this.$Bus.$on('clearSeleBuild', data => {
console.log(1);
_this.selectBuild = '';
});
},
methods: {
getBuilds1 () {
this.$Bus.$emit('showDialogs', true);
this.$Bus.$emit('curSelectBuild', {
name: '',
show: false
});
},
getBuilds (name) {
this.selectBuild = name;
this.$Bus.$emit('curSelectBuild', {
name: name,
show: true
});
}
}
};



