export default {
  data () {
    return {
      selectFloor: true,
      searchCurrentRom: 'T3.03'
    };
  },
  mounted () {
    this.$Bus.$on('curSearchBuild', data => {
      this.selectCurrentRom = data.name;
      this.selectFloor = data.show;
    });
  }
};



