
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Loading from 'vux/src/components/loading/index.vue'

import srcFloor1 from '../../assets/images/floor3.gif';
import srcFloor2 from '../../assets/images/floor.png';
import NoSatrtSelectMap from '../components/noSatrtSelectMap';
import OtherFloorMap from '../components/otherFloorMap.vue';
import CurrentPageFooter from '../components/currentPageFooter.vue';
import Page from '../components/page.vue';
import MyDia from '../components/dialog.vue';
import SearchBuilds from '../components/searchBuilds.vue';
import srclock from '../../assets/images/lock2.png';
export default {
name: 'mapSelects',
components: {
Flexbox,
Loading,
NoSatrtSelectMap,
OtherFloorMap,
CurrentPageFooter,
Page,
MyDia,
SearchBuilds
},
data () {
return {
srclock: srclock,
picImg: true,
pic: 0,
srcFloor: [srcFloor1, srcFloor2],
currentPage: '3rd',
show2: false,
selectFloor: true,
showDialog: false,
otherFloor: true
};
},
mounted () {
let _this = this;
this.$Bus.$on('changePagefloor', data => {
_this.show2 = true;
if (data === '1st' || data === '2nd') {
_this.otherFloor = true;
} else {
_this.otherFloor = false;
}
setTimeout(() => {
_this.show2 = false;
}, 1000);
});
},
methods: {
onPinchout () {
this.selectFloor = false;
},
lockFloor () {
this.showDialog = true;
},
colseDialog () {
this.showDialog = false;
},

changePage (val) {
let _this = this;
this.show2 = true;
this.selectFloor = false;
this.pic = 1;
setTimeout(() => {
_this.show2 = false;
_this.currentPage = val;
}, 1000);
}
}
};



