
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import srcFloor1 from '../../assets/images/floor-star.png';
import OtherFloorMap from '../components/otherFloorMap.vue';
import CurrentPageFooter from '../components/currentPageFooter.vue';
import Page from '../components/page.vue';
import MyDia from '../components/dialog.vue';

import { setTimeout } from 'timers';
export default {
name: 'information',
directives: {
TransferDom
},
components: {
Flexbox,
Loading,
XDialog,
FlexboxItem,
OtherFloorMap,
Page,
MyDia,
CurrentPageFooter

},
data () {
return {
srcFloor: srcFloor1,
show2: false,
selectFloor: false,
otherFloor: true
};
},
mounted () {
let _this = this;
this.$Bus.$on('changePagefloor', data => {
_this.show2 = true;
if (data === '1st' || data === '2nd') {
_this.otherFloor = true;
} else {
_this.otherFloor = false;
}
setTimeout(() => {
_this.show2 = false;
}, 1000);
});
},
methods: {
lockFloor () {
this.$Bus.$emit('showDialogs', true);
}
}
};



