export default {
  name: 'pagelist',
  data () {
    return {
      floors: ['1st', '2nd', '3rd', '4th', '5th', '6th', '7th'],
      currentPage: '1st'
    };
  },
  mounted () {
    // this.$Bus.$on('changePage', data => {
    //   this.currentPage = data;
    // });
  },
  methods: {
    // pagination
    changePage (val) {
      let _this = this;
      setTimeout(() => {
        _this.currentPage = val;
      }, 1000);
      this.$Bus.$emit('changePagefloor', val);
      this.$Bus.$emit('curSelectBuild', {name: null});
      this.$Bus.$emit('curSearchBuild', {name: null});
      this.$Bus.$emit('clearSeleBuild', {name: null});
    }
  }
};



