export default {
  name: 'currentSelectBuild',
  data () {
    return {
      selectBuild: false,
      buildType: null,
      selectCurrentRom: ''
    };
  },
  mounted () {
    this.$Bus.$on('curSelectBuild', data => {
      this.selectCurrentRom = data.name;
    });
  },
  methods: {}
};



